:root{
    --black: #222;
    --header: #045757;
    --bg-menu: #04575780;
    --bg-color: #044343;
    --light: #E4E4E4;
}
h1{
    cursor: pointer;
}
a{
    text-decoration: none;
    color: inherit;
}
*{
    font-family: 'Raleway', sans-serif;
}
body{
    background-color: whitesmoke;
    margin: 0;
}
.header{
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 20%;
    height: 3rem;
    max-width: 100%;

    position: sticky;
    top: 0;
    z-index: 1;

    background-color: var(--header);
    color: var(--light);
}
.icon__btn{
    cursor: pointer;
}
.header__title{
    font-weight: 700;
    font-size: 1.2rem;
    margin: 0;
}
/* NO RESPONSIVE */
.header__list{
    display: none;
}
.header__mobile__nav.no__display{
    left: 100%;
}

.header__mobile__nav{
    transition: all .5s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    
    position: fixed;
    top: 0;
    /* right: 0; */
    left: 0;
    z-index: 2;

    background-color: var(--bg-color);
}
.header__mobile__list{
    margin-bottom: 20vh;
    margin-top: 0;
    display: flex;
    flex-direction: column;
    list-style: none;
    align-items: center;
    padding: 0;
    gap: 2rem;
}
.header__mobile__list .header__list__item{
    text-decoration: underline;
}
.header__mobile__title{
    font-size: 1.5rem;
    color: var(--black);
    font-weight: 700;
    margin-bottom: 2rem;
    text-decoration: none;
}

.header__bg-responsive.no__display{
    display: none;
}
.header__bg-responsive{
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    background-color: var(--bg-menu);
}
.header__icon-close{
    border: 2px solid var(--light);
    border-radius: 50%;
    position: absolute;
    top: 1rem;
    right: 1rem;
    font-size: 2rem;
}



/* ***************************** */
/* ************ MAIN ***************** */

.main{
    min-height: calc(100vh - 3rem);
    width: 92%;
    margin: 0 auto;
}
.main__nav{
    display: none;
}






.basket__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
}
.basket__header__title{
    font-size: 1.2rem;
    margin: 0;
    color: var(--header);
}
.basket__btn{
    padding: .3rem .7rem;
    background-color: var(--bg-color);
    border-radius: 5px;
    border: none;
    color: wheat;
    font-size: 1rem;
    transition: all .2s ease;
}
.basket__btn:hover{
    background-color: var(--black);
}

.basket__items{
    font-size: .8rem;
    width: 100%;
    border-collapse: collapse;

}
.basket__items th{
    padding: 0;
    background-color: var(--header);
}

.basket__items__header{
    background-color: var(--header);
    border-bottom: 2px solid var(--black);
    height: 2rem;
    color: white;
    text-align: center;
}

.basket__items__header th:first-child{
    width: 50%;
}
.basket__items__header th{
    font-weight: 700;
    font-size: 1rem;
    padding-left: 0.2rem;
}

.basket__items__data{
    text-align: center;
}
.basket__items__data td:first-child{
    text-align: left;
    font-size: .7rem;
}
.basket__items__data span{
    font-size: 1rem;
}
.basket__items__data tr{
    border-bottom: .5px solid black;
    height: 1.5rem;
}
.basket__items__data tr:last-child{
    border-bottom: none;
}
.basket__items__data-actions{
    display: flex;
    justify-content: space-evenly;

    align-items: center;
    flex-wrap: wrap;
    height: 100%;
}

.basket__items__data-actions span{
    color: var(--bg-color);
    font-weight: bold;
}
.basket__items__data-actions span:last-child{
    font-weight: normal;
}
.basket__items__data-actions span:last-child:hover{
    color: red;
}

.basket__footer{
    margin-top: 1.5rem;
    display: flex;
    justify-content: space-between;
}
.basket__footer__total{
    margin: 0;
    font-weight: 700;
}



.search__container{
    height: 2rem;
    display: flex;
    border-radius: 2rem;
    border: 2px solid var(--bg-color);
    overflow: hidden;
    /* align-items: stretch; */
}
.search__container:focus-within{
    border: 2px solid var(--black);
}
.search__container input{
    width: 250px;
    border: none;
    outline: none;
    padding-left: .5rem;
}
.search__container label{
    background-color: var(--bg-color);
    /* background-color: var(--black); */
    width: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}


/* *************************** */
/* FOOTER   */
.footer{
    background-color: var(--bg-menu);
}